
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.site-info {
  color: blue;
  margin-bottom: 20px;
}
.btn-primary {
  margin-right: 5px;
}
.container-fluid {
  text-align: center;
}

.site-info {
  color: blue;
  margin-bottom: 20px;
}
.btn-primary {
  margin-right: 5px;
}

.login-row {
  clear: both;
  padding-top: 4%;
}
.login-form {
  margin: 0 auto;
  padding-top: 4%;
  padding-left: 10%;
  height: 400px;
  width: 50%;
  background-color: white;
  -webkit-box-shadow: 3px 3px 3px 3px #d0d0d0;
          box-shadow: 3px 3px 3px 3px #d0d0d0;
}
h1 {
  padding-bottom: 6%;
}
.login-form input {
  border-style: none;
  border-bottom: solid black 1px;
  height: 30px;
  width: 80%;
  padding-left: 2%;
  outline: none;
}
.login-form p:last-child {
  text-align: right;
  padding-right: 15%;
}
.login-form button {
  height: 35px;
  width: 80px;
  border: none;
  border-radius: 5px;
}
.login-form button:first-child {
  background: #33c197;
  color: white;
}

